.App {
  text-align: center;
  margin: 0;
}

.App-header {
  background-color: #282c34;
  height: 7vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(5px + 2vmin);
  color: white;
}

.App-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  min-height: 3vh;
  background-color: grey;
  font-size: calc(5px + 1vmin);
}

.Container-section {
  height: 90vh;
  width: 85%;
  background-color: red;
  overflow-y : scroll;
}
.Container-table {
  width: 100%;
}
tr,table,td,th {
  border: 1px solid black;
  border-collapse: collapse;
}
.Container-textarea {
  width: 100%;
  height: 85vh;
  box-sizing: border-box;
  background-color: #f8f8f8;
  font-size: 12px;
  resize: none;
}
textarea:focus {
  outline: none !important;
}
.App-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
}
.Container-aside {
  background-color: blue;
  width: 15%;
  height: 100vh;
}
.Button-container {
  display: flex;
  flex-direction: row;
  
}
